import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/', component: () => import('../views/home-base.vue'),
    children: [
      {
        path: '', component: () => import('../views/home-content.vue')
      },
      {
        path: ':sectionID', component: () => import('../views/section-base.vue'),
        children: [
          {
            path: '', component: () => import('../views/section-content.vue')
          },
          {
            path: ':subsectionID', component: () => import('../views/subsection-base.vue'),
            children: [
              {
                path: '', component: () => import('../views/subsection-content.vue')
              },
            ]
          },
        ]
      },
    ]
  },

  //404
  {
    path: '*',
    redirect: '/',
  },
  
]

const router = new VueRouter({
  routes
});

export default router;
